@font-face {
  font-family: "Roboto Slab";
  src: url("../fonts/RobotoSlabRegular.woff2") format("woff2"), url("../fonts/RobotoSlabRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Roboto Slab";
  src: url("../fonts/RobotoSlabBold.woff2") format("woff2"), url("../fonts/RobotoSlabBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  src: url("../fonts/WorkSansRegular.woff2") format("woff2"), url("../fonts/WorkSansRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  src: url("../fonts/WorkSansBold.woff2") format("woff2"), url("../fonts/WorkSansBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

html {
  scroll-behavior: smooth; }

body {
  font-family: "Work Sans", "Arial", sans-serif;
  box-sizing: border-box; }

img {
  width: 100%;
  height: auto; }

p {
  margin: 0; }

ul {
  margin: 0;
  padding: 0; }

a {
  display: flex;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: inherit;
  justify-content: center;
  align-items: center; }

.body-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #363636;
  margin: 0 auto; }

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 375px;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.main-header {
  padding-top: 39px;
  padding-bottom: 43px; }

.main-header__title {
  font-weight: bold;
  font-size: 36px;
  line-height: 49px;
  color: #45852D;
  margin: 0;
  margin-left: 399px; }

@media (max-width: 1279px) {
  .main-header__title {
    margin-left: 120px; } }

@media (max-width: 767px) {
  .main-header__title {
    margin: 0; } }

.main-body {
  padding: 0 50px; }

.main-body__filter-block {
  display: flex;
  align-items: center;
  margin-bottom: 45px; }

.main-body__aparts {
  margin-bottom: 22px; }

.main-body__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  min-height: 50px;
  margin: 0 auto;
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  background-color: transparent;
  border: 1px solid #D8D8D8;
  border-radius: 50px;
  transition-duration: 0.3s; }
  .main-body__button:hover, .main-body__button:focus {
    border-color: #007aff; }
  .main-body__button:active {
    transform: scale(0.9); }
  .main-body__button svg {
    margin-left: 5px;
    padding-top: 2px; }

@media (max-width: 1279px) {
  .main-body {
    padding: 0; }
  .main-body__filter-block {
    margin-left: 20px; } }

@media (max-width: 767px) {
  .main-body__filter-block {
    margin-left: 0; } }

.filter-block__text {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  margin-left: 1px;
  margin-right: 20px; }

.filter-block__input {
  width: 386px;
  height: 50px;
  border-radius: 50px;
  border: 1px solid #D8D8D8;
  outline: none;
  transition-duration: 0.3s;
  padding: 0 15px; }
  .filter-block__input:hover, .filter-block__input:focus {
    border-color: #007aff; }

.filter-block__error-message {
  display: none;
  margin-left: 20px;
  padding: 17px;
  background-color: #FFCCCC;
  border-radius: 5px;
  font-weight: 700; }

.card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  box-sizing: border-box;
  outline: none;
  border: 1px solid #D8D8D8;
  transition-duration: 0.3s; }
  .card:hover, .card:focus {
    border: 1px solid #007aff; }
  .card:active {
    opacity: 0.7; }

.card__image-block {
  position: relative;
  height: 230px;
  width: 100%;
  margin-bottom: 17px;
  background-color: brown; }

.card__image {
  height: 100%;
  object-fit: cover; }

.card__type {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 118px;
  min-height: 14px;
  padding: 8px;
  background-color: #006F79;
  color: #ffffff;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  text-align: center; }
  .card__type--orange-background {
    background-color: #EC6608;
    width: 188px; }

.card__text-block {
  padding: 0 20px; }

.card__title {
  margin: 0;
  margin-bottom: 2px;
  font-weight: bold;
  font-size: 20px;
  line-height: 27px; }

.card__address {
  margin-bottom: 15px;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px; }

.card__price-text {
  margin-bottom: 7px;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px; }

.card__price {
  font-weight: bold; }

.card__shared-type {
  margin-bottom: 17px;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px; }

.aparts-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.aparts-list__item {
  width: 379px;
  min-height: 381px;
  margin-right: 21px;
  margin-bottom: 38px; }
  .aparts-list__item:nth-child(3n) {
    margin-right: 0; }

@media (max-width: 1279px) {
  .aparts-list__item {
    margin-right: 10px;
    margin-bottom: 28px; }
    .aparts-list__item:nth-child(2n) {
      margin-right: 0; }
    .aparts-list__item:nth-child(3n) {
      margin-right: auto; } }

@media (max-width: 767px) {
  .aparts-list__item {
    margin-right: 0; } }

.notification {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  min-height: 200px;
  margin: 0 auto; }

.notification__image {
  width: 100px;
  height: 100px; }

@media (max-width: 1279px) {
  .notification {
    width: 320px; } }
